  .about{
    display: flex;
    flex-direction: column;
  }
  
  .aboutImg{
    display: flex;
    justify-content: center;
  }
  
  .cards-project{
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  }
  
  @media screen and (max-width: 750px){
