      flex-direction: column;
      padding: 0 30px;
      justify-content: center;
      gap: 5rem;
    }
    .cards-project{
      display: flex;
      flex-wrap: wrap;
    }
    .join .button{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .artikels{
      justify-content: center;
    }
